#navigatie {
    background: #000;
    position: relative;
    width: 100%;

    ul {
        list-style: none;
        padding: 10px;
        background: #272727;
        display: inline-block;

        li {
            float: left;
            padding: 10px;
            margin-left: 10px;
            background: #5a5a5a;
        }

        a {
            color: #fff;
            text-decoration: none;
        }

    }
}